@import "../../variables";

.popup_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
.popup {
  background: $bg-color;
  padding: 60px 40px 20px 40px;
  border-radius: 30px;
  position: relative;
  max-height: 80%;;
  width: 62.5%;
  display: flex;
  flex-direction: column;
}

.popup_content {
  overflow-y: auto;
  flex-grow: 1;
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
}

.popup-content::-webkit-scrollbar { 
  display: none;  /* Safari and Chrome */
}
  
.close_button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
}

.no-scroll {
  overflow: hidden;
}
  

.close_icon {
  padding-top: 0.5rem;
  padding-right: .5rem;
  color: $icon;
  transition: .2s linear;
}

.close_icon:hover {
  color: $icon-hovered;
}

@media (max-width: 700px) {
  .popup {
    width: 90%;
    border-radius: 30px;
    padding: 0;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  .popup_content {
    width: 100%;
  }
  
  .close_icon {
    padding-top: 10px;
    padding-right: 10px;
  }

}